import React from 'react'
import { View } from 'react-native'
import Image from './Image';
import Link from './gatsby-link'
import StyleSheet from './StyleSheet';
import FacebookIcon from './svgs/facebook';
import TumblrIcon from './svgs/tumblr';
import TwitterIcon from './svgs/twitter';
import InstagramIcon from './svgs/instagram';


const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    alignItems: 'flex-end',
    borderBottomWidth: 3,
    borderBottomColor: '$color3',
    backgroundColor: '$color4',
  },
  link: {
    color: '$color2',
    fontFamily: 'BodyFont',
    marginRight: 25,
    marginBottom:5,
    fontSize: 15,
    textDecorationLine: 'none',

  },
  activeLink: {
    color: '$color1',
  },
  linkCollection: {
    flexDirection: 'row',
  },
  face: {
    marginHorizontal:20,
    marginTop:20,
    width: 70,
    height:70,
  },
  adsf: {
    height: 30,
    width: 30,
    backgroundColor:'red',
  }

})


const config = [
  {
    Icon: FacebookIcon,
    props: {},
    url: 'https://www.facebook.com/welcometolifecast',
  },
  {
    Icon: TwitterIcon,
    props: {},
    url: 'https://twitter.com/marcopsal'
  },
  {
    Icon: InstagramIcon,
    props: {},
    url: 'https://www.instagram.com/marcopsal'
  },
  {
    Icon: TumblrIcon,
    props: {},
    url: 'https://marcopsal.tumblr.com/'
  },
];

const socialIconsComponents = config.map(({Icon,props,url}) => {
  return () => {
    return (
      <a href={url} target="_blank" style={{display:'block'}}>
        <Icon
          {...props}
        />
      </a>
    );
  };
});


const SocialButtons = () => (
  <View style={styles.container}>
    {
      socialIconsComponents.map(Comp => (
        <Comp />
      ))
    }
  </View>
)

export default SocialButtons;
